
import FormPreview from 'vux/src/components/form-preview/index.vue'


export default {
components: {
FormPreview
},
data () {
return {
list: '',//学生所有信息
info:[],//页面显示信息
}
},
created(){
this.getList()
},
methods:{
getList() {
console.log('用户信息：')
console.log(this.$store)
this.$http.get('http://139.199.168.158:8080/admin/supervisormystudents?supervisorname='+this.$store.getters.username).then((response) => {
console.log('获取学员姓名及课时情况')
console.log(response)
response = response.data
if (response.code === 1) {
console.log('获取我的学生成功')


















}
})

this.listLoading = false
},
}
}



